.Button {
  display: inline-block;
  padding: 10px 20px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  border: 1px solid #cccccc;
  color: #000;
  margin-right: 15px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
  cursor: pointer;
  &:focus {
    outline: none; }
  &:active {
    box-shadow: inset 2px 1px 1px rgba(0, 0, 0, .3); }
  &:disabled {
    background: #cccccc;
    color: #000;
    cursor: not-allowed; } }

.error {
  background: rgba(240, 87, 108, 1); }
.success {
  background: rgba(161, 240, 69, 1); }

.primary {
  background: #2884f6;
  color: #ffffff; }
