.QuizList {
  display: flex;
  justify-content: center;
  padding-top: 100px;
  flex-grow: 1;
  width: 100%;
  background: linear-gradient(90deg, #fd8335 0%, #f0576c 37%, #f79cbd 100%);
  h1 {
    color: #ffffff; }
  ul {
    list-style: none;
    text-align: center;
    margin: 0;
    padding: 0; }
  li {
    margin-bottom: 10px;
    a {
      text-decoration: none;
      color: #ffffff;
      font-size: 18px;
      transition: all .5s ease;
      &:hover {
        color: #363f54; } } } }

