.AnswerItem {
  border: 1px solid #ffffff;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  padding: 5px 10px;
  margin-bottom: 5px;
  cursor: pointer;
  &:hover {
    background: rgba(255, 255, 255, .2);
    transition: background .3s ease-in-out; }
  &.success {
    background: rgba(161, 240, 69, .7); }
  &.error {
    background: rgba(240, 87, 108, .7); } }
